#div-header {
	position: fixed;
	top: 0;
	left: 0;
	bottom: 0;
	width: 100%;
	background-color: var(--primary-color);
	padding: 10px 0;
	box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.1);
	height: 60px;
	z-index: 5;
}

#div-header .container {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
}

#div-header .container > div {
	display: flex;
	flex-direction: row;
	align-items: center;
}

#div-header #p-logo {
	color: white;
}

#div-header img {
	height: 40px;
	margin-right: 20px;
}

#div-header h3 {
	color: white;
	min-width: 100px;
}

#div-header #btn-logout {
	margin-left: 20px;
	color: white;
}

#div-header #btn-logout:hover {
	opacity: 0.8;
}

#div-header .material-icons {
	background-color: white;
	color: var(--primary-color);
	font-size: 1.5rem;
	border-radius: 5px;
	padding: 2px;
}
