@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#div-header {
	position: fixed;
	top: 0;
	left: 0;
	bottom: 0;
	width: 100%;
	background-color: var(--primary-color);
	padding: 10px 0;
	box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.1);
	height: 60px;
	z-index: 5;
}

#div-header .container {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	-webkit-align-items: center;
	        align-items: center;
}

#div-header .container > div {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
	-webkit-align-items: center;
	        align-items: center;
}

#div-header #p-logo {
	color: white;
}

#div-header img {
	height: 40px;
	margin-right: 20px;
}

#div-header h3 {
	color: white;
	min-width: 100px;
}

#div-header #btn-logout {
	margin-left: 20px;
	color: white;
}

#div-header #btn-logout:hover {
	opacity: 0.8;
}

#div-header .material-icons {
	background-color: white;
	color: var(--primary-color);
	font-size: 1.5rem;
	border-radius: 5px;
	padding: 2px;
}

.form {
	position: absolute;
	top: 100px;
	right: 0;
	border-radius: 5px;
	width: 400px;
	height: -webkit-max-content;
	height: max-content;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	padding: 40px;
	background-color: white;
	box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.1);
}

.form h2 {
	color: var(--primary-color);
	margin-bottom: 40px;
	text-align: center;
}

.form input {
	font-size: 1rem;
	margin-bottom: 20px;
	border: none;
	border-bottom: 1px solid var(--dark-color);
	color: var(--secondary-color);
	outline: none;
}

.form button,
#btn-login {
	border: none;
	font-size: 1rem;
	width: -webkit-fit-content;
	width: -moz-fit-content;
	width: fit-content;
}

.form .button-link {
	color: var(--primary-color);
	text-decoration: underline;
	background: none;
}

.form .button-link {
	margin-bottom: 10px;
}

.form > *:last-child {
	display: inline-block;
	background-color: var(--primary-color);
	color: white;
	padding: 10px;
	border-radius: 5px;
	margin-top: 10px;
	text-decoration: none;
}

.form #btn-login:hover {
	opacity: 0.8;
}

.form p small {
	color: red;
}

.form p:last-of-type {
	margin-bottom: 10px;
}

#div-login-page {
	position: relative;
	width: 100%;
	height: 500px;
}

#div-login-page img {
	position: absolute;
	top: 100px;
	left: 0;
	width: 350px;
}

#div-user-page {
	background-color: white;
	min-height: 100vh;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-align-items: center;
	        align-items: center;
	padding-bottom: 50px;
}

#div-post-form {
	width: 75%;
	padding: 40px 0 20px 0;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
	-webkit-align-items: flex-start;
	        align-items: flex-start;
	border-bottom: 2px solid var(--light-color);
}

#div-post-form > div {
	width: 90%;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
}

#div-post-form img {
	width: 50px;
	margin-right: 20px;
}

#div-post-form textarea {
	resize: none;
	min-height: 70px;
	border-radius: 5px;
	border: 0;
	background-color: var(--light-color);
	color: var(--secondary-color);
	padding: 20px;
	outline: 0;
	-ms-overflow-style: none; /* IE and Edge */
	scrollbar-width: none; /* Firefox */
	transition: min-height 0.5s 0.1s ease;
	white-space: pre-line;
	font-size: 1rem;
}

#div-post-form textarea::-webkit-scrollbar {
	display: none;
}

#div-post-form textarea:focus {
	min-height: 120px;
}

#div-post-form button {
	margin: 0 0 0 auto;
	border: none;
	font-size: 1rem;
	width: -webkit-fit-content;
	width: -moz-fit-content;
	width: fit-content;
	background-color: var(--primary-color);
	color: white;
	padding: 5px 10px;
	border-radius: 5px;
	margin-top: 20px;
}

#div-post-form textarea.border-primary-color {
	border: 1px solid red;
}

#div-comment-form {
	position: relative;
	width: 100%;
}

#div-comment-form img {
	width: 30px;
}

#div-comment-form textarea {
	margin-left: 10px;
	width: 90%;
	resize: none;
	height: 30px;
	border-radius: 5px;
	border: 0;
	background-color: var(--light-color);
	color: var(--secondary-color);
	padding: 5px;
	outline: 0;
	-ms-overflow-style: none; /* IE and Edge */
	scrollbar-width: none; /* Firefox */
	white-space: pre-line;
	padding-right: 40px;
}

#div-comment-form textarea::-webkit-scrollbar {
	display: none;
}

#div-comment-form button {
	position: absolute;
	top: 5px;
	right: 10px;
	height: 20px;
	width: 20px;
	background-color: var(--primary-color);
	color: white;
	border-radius: 50%;
	border: 0;
	outline: 0;
}

#div-comment-form span {
	font-size: 1rem;
}

#div-comment-form textarea.border-primary-color {
	border: 1px solid red;
}

.div-comment {
	padding: 0 20px;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
	-webkit-align-items: flex-start;
	        align-items: flex-start;
	margin-bottom: 10px;
}

.div-comment > img {
	width: 30px;
}

.div-comment .div-comment-inner-container {
	width: 100%;
	padding-left: 10px;
}

.div-comment .div-comment-inner-container h5 {
	color: var(--primary-color);
}

.div-comment .div-comment-inner-container h5 small {
	color: black;
	margin-left: 5px;
}

.div-comment .div-comment-inner-container p {
	position: relative;
	margin-top: 5px;
	border-radius: 5px;
	background-color: var(--light-color);
	padding: 5px 20px 5px 5px;
	white-space: pre-wrap;
	word-wrap: break-word;
	margin-bottom: 10px;
}

.div-comment .div-comment-inner-container button {
	position: absolute;
	bottom: 5px;
	right: 5px;
	height: 20px;
	width: 20px;
	background-color: var(--primary-color);
	color: white;
	border-radius: 50%;
	border: 0;
	outline: 0;
}

.div-comment .div-comment-inner-container button span {
	font-size: 1rem;
}

.div-comment .div-comment-inner-container .p-title-replies {
	background: none;
	font-weight: 600;
	margin-bottom: 5px;
}

.div-reply-comment {
	padding: 0 20px;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
	-webkit-align-items: flex-start;
	        align-items: flex-start;
	margin-bottom: 10px;
}

.div-reply-comment > img {
	width: 30px;
}

.div-reply-comment .div-comment-inner-container {
	width: 100%;
	padding-left: 10px;
}

.div-reply-comment .div-comment-inner-container h5 {
	color: var(--primary-color);
}

.div-reply-comment .div-comment-inner-container h5 small {
	color: black;
	margin-left: 5px;
}

.div-reply-comment .div-comment-inner-container p {
	position: relative;
	margin-top: 5px;
	border-radius: 5px;
	background-color: var(--light-color);
	padding: 5px 20px 5px 5px;
	white-space: pre-wrap;
	word-wrap: break-word;
}

#div-reply-comment-form {
	position: relative;
	width: 100%;
	margin-top: 10px;
}

#div-reply-comment-form img {
	width: 30px;
}

#div-reply-comment-form textarea {
	position: relative;
	margin-left: 10px;
	width: 85%;
	resize: none;
	height: 30px;
	border-radius: 5px;
	border: 0;
	background-color: var(--light-color);
	color: var(--secondary-color);
	padding: 5px;
	outline: 0;
	-ms-overflow-style: none; /* IE and Edge */
	scrollbar-width: none; /* Firefox */
	white-space: pre-line;
	padding-right: 40px;
}

#div-reply-comment-form textarea::-webkit-scrollbar {
	display: none;
}

#div-reply-comment-form button {
	position: absolute;
	top: 5px;
	right: 20px;
	height: 20px;
	width: 20px;
	background-color: var(--primary-color);
	color: white;
	border-radius: 50%;
	border: 0;
	outline: 0;
}

#div-reply-comment-form span {
	font-size: 1rem;
}

#div-reply-comment-form textarea.border-primary-color {
