.div-post {
	width: 60%;
	margin: 30px auto 0 auto;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	border-bottom: 2px solid var(--light-color);
	padding-bottom: 30px;
}

.div-post .div-post-header {
	width: 93%;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
}

.div-post .div-post-header > img {
	width: 40px;
}

.div-post .div-post-header h4 {
	margin-left: 10px;
	color: var(--primary-color);
}

.div-post .div-post-header h4 small {
	color: var(--secondary-color);
}

.div-post .p-post {
	background-color: var(--light-color);
	margin-top: 10px;
	padding: 10px;
	border-radius: 5px;
	white-space: pre-line;
	word-wrap: break-word;
}

.div-post .div-button-group {
	width: 30%;
	margin: 0 auto;
	padding: 5px;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-around;
	        justify-content: space-around;
}

.div-post .div-button-group button {
	background: none;
	color: var(--primary-color);
	border: 0;
}

.div-post .div-comments-container {
	min-height: 40px;
	width: 75%;
	-webkit-align-self: center;
	        align-self: center;
}

.div-post .div-comments-container p:first-child {
	font-weight: 600;
	margin-bottom: 5px;
}

#div-body {
	position: relative;
	top: 60px;
	margin-left: 50%;
	-webkit-transform: translateX(-50%);
	        transform: translateX(-50%);
}

:root {
	--primary-color: #ff3b3f;
	--secondary-color: #0a0a0a;
	--dark-color: #a9a9a9;
	--light-color: #efefef;
}

* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
	font-family: "Montserrat", sans-serif;
	-webkit-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
}

body {
	background-color: #efefef;
	background-color: var(--light-color);
	max-width: 100%;
	max-height: 100vh;
}

button {
	cursor: pointer;
}

button:hover {
	opacity: 0.85;
}

.container {
	width: 75%;
	margin: 0 auto;
}

