.form {
	position: absolute;
	top: 100px;
	right: 0;
	border-radius: 5px;
	width: 400px;
	height: max-content;
	display: flex;
	flex-direction: column;
	padding: 40px;
	background-color: white;
	box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.1);
}

.form h2 {
	color: var(--primary-color);
	margin-bottom: 40px;
	text-align: center;
}

.form input {
	font-size: 1rem;
	margin-bottom: 20px;
	border: none;
	border-bottom: 1px solid var(--dark-color);
	color: var(--secondary-color);
	outline: none;
}

.form button,
#btn-login {
	border: none;
	font-size: 1rem;
	width: fit-content;
}

.form .button-link {
	color: var(--primary-color);
	text-decoration: underline;
	background: none;
}

.form .button-link {
	margin-bottom: 10px;
}

.form > *:last-child {
	display: inline-block;
	background-color: var(--primary-color);
	color: white;
	padding: 10px;
	border-radius: 5px;
	margin-top: 10px;
	text-decoration: none;
}

.form #btn-login:hover {
	opacity: 0.8;
}

.form p small {
	color: red;
}

.form p:last-of-type {
	margin-bottom: 10px;
}
