#div-comment-form {
	position: relative;
	width: 100%;
}

#div-comment-form img {
	width: 30px;
}

#div-comment-form textarea {
	margin-left: 10px;
	width: 90%;
	resize: none;
	height: 30px;
	border-radius: 5px;
	border: 0;
	background-color: var(--light-color);
	color: var(--secondary-color);
	padding: 5px;
	outline: 0;
	-ms-overflow-style: none; /* IE and Edge */
	scrollbar-width: none; /* Firefox */
	white-space: pre-line;
	padding-right: 40px;
}

#div-comment-form textarea::-webkit-scrollbar {
	display: none;
}

#div-comment-form button {
	position: absolute;
	top: 5px;
	right: 10px;
	height: 20px;
	width: 20px;
	background-color: var(--primary-color);
	color: white;
	border-radius: 50%;
	border: 0;
	outline: 0;
}

#div-comment-form span {
	font-size: 1rem;
}

#div-comment-form textarea.border-primary-color {
	border: 1px solid red;
}
