#div-post-form {
	width: 75%;
	padding: 40px 0 20px 0;
	display: flex;
	flex-direction: row;
	align-items: flex-start;
	border-bottom: 2px solid var(--light-color);
}

#div-post-form > div {
	width: 90%;
	display: flex;
	flex-direction: column;
}

#div-post-form img {
	width: 50px;
	margin-right: 20px;
}

#div-post-form textarea {
	resize: none;
	min-height: 70px;
	border-radius: 5px;
	border: 0;
	background-color: var(--light-color);
	color: var(--secondary-color);
	padding: 20px;
	outline: 0;
	-ms-overflow-style: none; /* IE and Edge */
	scrollbar-width: none; /* Firefox */
	transition: min-height 0.5s 0.1s ease;
	white-space: pre-line;
	font-size: 1rem;
}

#div-post-form textarea::-webkit-scrollbar {
	display: none;
}

#div-post-form textarea:focus {
	min-height: 120px;
}

#div-post-form button {
	margin: 0 0 0 auto;
	border: none;
	font-size: 1rem;
	width: fit-content;
	background-color: var(--primary-color);
	color: white;
	padding: 5px 10px;
	border-radius: 5px;
	margin-top: 20px;
}

#div-post-form textarea.border-primary-color {
	border: 1px solid red;
}
