.div-comment {
	padding: 0 20px;
	display: flex;
	flex-direction: row;
	align-items: flex-start;
	margin-bottom: 10px;
}

.div-comment > img {
	width: 30px;
}

.div-comment .div-comment-inner-container {
	width: 100%;
	padding-left: 10px;
}

.div-comment .div-comment-inner-container h5 {
	color: var(--primary-color);
}

.div-comment .div-comment-inner-container h5 small {
	color: black;
	margin-left: 5px;
}

.div-comment .div-comment-inner-container p {
	position: relative;
	margin-top: 5px;
	border-radius: 5px;
	background-color: var(--light-color);
	padding: 5px 20px 5px 5px;
	white-space: pre-wrap;
	word-wrap: break-word;
	margin-bottom: 10px;
}

.div-comment .div-comment-inner-container button {
	position: absolute;
	bottom: 5px;
	right: 5px;
	height: 20px;
	width: 20px;
	background-color: var(--primary-color);
	color: white;
	border-radius: 50%;
	border: 0;
	outline: 0;
}

.div-comment .div-comment-inner-container button span {
	font-size: 1rem;
}

.div-comment .div-comment-inner-container .p-title-replies {
	background: none;
	font-weight: 600;
	margin-bottom: 5px;
}
