.div-reply-comment {
	padding: 0 20px;
	display: flex;
	flex-direction: row;
	align-items: flex-start;
	margin-bottom: 10px;
}

.div-reply-comment > img {
	width: 30px;
}

.div-reply-comment .div-comment-inner-container {
	width: 100%;
	padding-left: 10px;
}

.div-reply-comment .div-comment-inner-container h5 {
	color: var(--primary-color);
}

.div-reply-comment .div-comment-inner-container h5 small {
	color: black;
	margin-left: 5px;
}

.div-reply-comment .div-comment-inner-container p {
	position: relative;
	margin-top: 5px;
	border-radius: 5px;
	background-color: var(--light-color);
	padding: 5px 20px 5px 5px;
	white-space: pre-wrap;
	word-wrap: break-word;
}
