.div-post {
	width: 60%;
	margin: 30px auto 0 auto;
	display: flex;
	flex-direction: column;
	border-bottom: 2px solid var(--light-color);
	padding-bottom: 30px;
}

.div-post .div-post-header {
	width: 93%;
	display: flex;
	flex-direction: row;
}

.div-post .div-post-header > img {
	width: 40px;
}

.div-post .div-post-header h4 {
	margin-left: 10px;
	color: var(--primary-color);
}

.div-post .div-post-header h4 small {
	color: var(--secondary-color);
}

.div-post .p-post {
	background-color: var(--light-color);
	margin-top: 10px;
	padding: 10px;
	border-radius: 5px;
	white-space: pre-line;
	word-wrap: break-word;
}

.div-post .div-button-group {
	width: 30%;
	margin: 0 auto;
	padding: 5px;
	display: flex;
	justify-content: space-around;
}

.div-post .div-button-group button {
	background: none;
	color: var(--primary-color);
	border: 0;
}

.div-post .div-comments-container {
	min-height: 40px;
	width: 75%;
	align-self: center;
}

.div-post .div-comments-container p:first-child {
	font-weight: 600;
	margin-bottom: 5px;
}
